#overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;

    font-size: 28px;
    background: linear-gradient( 110.1deg,  rgba(30,2,83,1) 44.2%, rgba(198,55,160,1) 138.2% );
}
#users {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;

    font-family: 'Smooch Sans', sans-serif;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .userBlock {
        box-sizing: border-box;
        padding: 1em;

        filter: drop-shadow(0 0 0.75rem yellow);
        background: linear-gradient(90deg, rgba(148,187,233,1) 0%, rgba(238,174,202,0) 100%);
        
        &:nth-child(2n) {
            text-align: right;
            background: linear-gradient(90deg, rgba(238,174,202,0) 0%, rgba(148,187,233,1) 100%);
        
            .userRow {
                transform-origin: right;
            }
        }

        .user_title {
            font-size: 1.5em;
            padding: 0.25em 0;
            border-bottom: 2px solid rgba(white, 0.5);
            margin-bottom: 0.5em;

            font-weight: 700;
        }
    }

    .userRow {
        transition: 0.25s transform ease 0.5s opacity ease 2s;
        transform: translateX(0) scale(1);
        transform-origin: left;

        &:not(.current):not(.final) {
            opacity: 0.5;
            transform: translateX(0) scale(0.8);
        }

        &.done, &.final {
            animation: userRowFinish;
            animation-duration: 0.5s;
            animation-iteration-count: 3;
        }

        .userRow_title {
            font-weight: 600;
        }
        .userRow_time {
            font-variant: slashed-zero;
            font-family: 'Share Tech Mono', monospace;
        }
    }
}

@keyframes userRowFinish {
    0% {
        color: inherit;
    }
    50% {
        color: red;
    }
    100% {
        color: inherit;
    }

}